@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700&family=Work+Sans:wght@300;400;500&display=swap);

#midDiv {position: relative;z-index: 1;}
.projects{padding:50px 0 30px;}

.projects h2{display: inline-block;margin-bottom:15px;}
.projects h2:after{content:"";height:1px; width:75%; margin:5px auto 0px; max-width: 250px; background-color: #ff617f;display: block;}  

.projects .tagList{max-width:80%;margin:auto;}
.tagList .tag-item{cursor:pointer;background-color:#ff617f;font-size: 0.75rem; min-width: 50px;display: inline-block; color:#fff; transition:all 0.25s;padding:5px; margin:5px;border-radius:5px;border:1px outset #ff617f; letter-spacing: 2.5px;}
.tagList .tag-item.sel{background-color: #fff;color:#ff617f;border-style:solid;}
.tagList .tag-item:hover{background-color:#fff; transition:all 0.25s; border-style:solid;  color:#ff617f;}
.tagList .tag-item.sel:hover{cursor: default;}

.projects > .desc{margin: 15px 0 25px;}

.projects .projectWrap{max-width: 80%;margin:auto;}
.projects .project-item{display:inline-block;width:47.5%;margin:0 2.5% 75px;position:relative;box-shadow:0px 0px 20px 0 #bbb;overflow: hidden; transition: box-shadow 0.5s;}
.projects .project-item:nth-child(odd){margin-left:-2.5%;}
.projects .project-item:nth-child(even){margin-right:-2.5%;}
.projects .project-item:hover{box-shadow: 0px 0px 30px 0 #888;transition:  box-shadow 0.5s;}
.projects .item-content{padding:50px 60px; z-index: 2;position: absolute;top:0;bottom:0;left:0;right:0;text-align: left; color: #fff;text-shadow: 0px 0px 5px #000;}
.projects .project-item .contentWrap{position: absolute;top:50%; transform:translateY(-50%); padding: 50px 60px;  margin:auto; left:0; right:0; text-align: center; transform-origin: center; transition: transform 0.25s;}
.projects .project-item:hover .contentWrap{transform: scale(1.075)  translateY(-50%); transition: transform 0.25s;}
.projects .miscWrap{position: absolute;bottom:0;   margin:auto; left:0; right:0; text-align: center;}
.projects .project-item .overlay{background-image: linear-gradient(90deg, rgba(255, 97, 127, 0.75) 0%, rgb(213, 127, 143) 100%);background-color:rgba(75,75,75, 0.8);width:100%;background-size:0% 100%;background-position:right center;transition:background-size 0.25s, background-color 0.25s;}
.projects .project-item:hover .overlay{background-size:100% 100%; background-color:rgba(175,175,175, 0.6); transition: background-size 0.5s 0.35s, background-color 0.25s;}

.projects .project-item img{max-width: 100%;display: block;}

.projects .contentWrap h3{text-transform: none;color:#fff;font-size: 2.25rem; font-weight: 800;text-shadow: 0px 0px 5px #000;margin:0; font-family: 'Open Sans', sans-serif;}
.projects .contentWrap p{letter-spacing:3px;margin:5px 0;}

.projects .miscWrap .tags {display: block;margin-bottom:15px;padding: 0 60px;}
.projects .miscWrap .tags span{background-color:#ff617f; opacity: 0; transition:all 0.25s; border:1px solid #ff617f;  color:#fff;font-size: 0.75rem; min-width: 60px;display: inline-block;padding:5px; margin:3px 5px;border-radius:5px;text-align: center;text-shadow:none; letter-spacing: 2.25px; transition: opacity 0.25s;}
.projects .project-item:hover .tags span{opacity:1;transition: opacity 0.5s 0.75s;}

.projects .miscWrap .btn {position: relative; bottom:-41px; width: 100%;border-radius:0;border:0px none;box-sizing:border-box;}
.projects .project-item:hover .btn{bottom:0; color:#fff; transition: bottom 0.5s 0.75s,background-color 0.25s;}
.projects .project-item:hover .btn:hover{background-color: #f75371;transition:background-color 0.25s;}

@media screen and (max-width: 1800px) {
    .projects .contentWrap{padding: 40px 50px;}
    .projects .contentWrap h3{font-size: 2rem;}
    .projects .contentWrap p{font-size: 0.9rem;}
    .projects .miscWrap .tags{padding:0 50px;}
    .projects .miscWrap .tags span{min-width: 45px;}

}

@media screen and (max-width: 1700px) {
    .projects .contentWrap{padding: 35px 45px;}
    .projects .miscWrap .tags{padding:0 45px;}
    .projects .miscWrap .tags span{min-width: 40px;font-size: 0.6rem;}
}

@media screen and (max-width: 1600px) {
    .projects .contentWrap{padding: 25px 30px;}
    .projects .miscWrap .tags{padding:0 30px;}
    .projects .projectWrap{max-width: 90%;}
    .projects .tagList{max-width: 90%;}

}

@media screen and (max-width: 1440px) {
    .projects .tagList{max-width:none;padding:0 50px;}
    .projects .projectWrap{max-width:none;padding:0 50px;}
    .projects .project-item .contentWrap{ padding: 5%;}
    .projects .contentWrap h3{font-size:1.75rem;}
    .projects .miscWrap .tags{padding:0 5%;}
    .projects .contentWrap p{font-size:0.8rem;}
    .projects .miscWrap .tags span{padding: 3px 4px;  border-radius: 3px;  margin: 3px 3px;}

}

@media screen and (max-width: 1280px) {
    .projects .project-item{display: block;width:80%;}
    .projects .project-item:nth-child(odd), .projects .project-item:nth-child(even){margin:0 auto 50px;}

    .projects .projectWrap{max-width:none;padding:0 25px;}
    .projects .tagList{max-width:none;padding:0 50px;}
    .projects .project-item .contentWrap{ padding: 5%;}
    .projects .contentWrap h3{font-size:2.5rem;}
    .projects .miscWrap .tags{padding:0 5%;}
    .projects .contentWrap p{font-size:1rem;}
    .projects .miscWrap .tags span{padding: 5px;  margin: 5px;font-size: 0.75rem;}

}

@media screen and (max-width: 1152px) {
}

@media screen and (max-width: 1024px) {
    .tagList .tag-item{font-size:0.65rem;min-width:40px;padding:4px;margin:4px;border-radius:3px;letter-spacing:2px;}
}

@media screen and (max-width: 960px) {
    .projects .tagList{padding:0 25px;}
    .projects .project-item{width:90%;}

}

@media screen and (max-width: 874px) {
    .projects .item-content{    padding: 5%;}
    .projects .project-item{width:100%;}
    .projects .contentWrap h3{font-size:2rem;}
    .projects .contentWrap p{font-size:0.9rem;}


}

@media screen and (max-width: 769px) {
    .projects .miscWrap .tags{margin-bottom: 10px;}
    .projects .miscWrap .tags span{padding:3px;margin:3px;font-size:0.6rem;}

}

@media screen and (max-width: 640px) {
    .projects .project-item{overflow: visible;}
    .projects .project-item:hover .btn{bottom:-32px; transition: none;}
    .projects .project-item:nth-child(odd),.projects .project-item:nth-child(even){margin:0 auto 75px;}
    .projects .item-content{ top: 50%; transform:translateY(-50%);bottom:auto;}
    
    .projects .project-item:hover .contentWrap{transform: none; transition:none;}
    .projects .project-item .contentWrap{position: static;transform:none;padding:0;}
    .projects .contentWrap p{display: none;}
    .projects .miscWrap {top:0; position: static;}
    .projects .miscWrap .btn{bottom: -32px;left:0;position: static;width:auto;border-radius:5px;}
    .projects .miscWrap .tags{position:static;padding:0;width:100%;box-sizing:border-box;margin:0 -3px;margin:10px -3px;}
    .projects .miscWrap .tags span{opacity: 1;border-color:#fff;color:#fff;background-color: rgba(0, 0, 0, 0.3);}
    .projects .project-item .overlay{ background-color:rgba(0,0,0, 0.55); transition: background-color 0.25s;;}
    .projects .project-item:hover .overlay{background-image: none; background-color:rgba(0,0,0, 0.45); transition:background-color 0.25s;}
    .projects .contentWrap h3{font-size:1.5rem;}

}


@media screen and (max-width: 480px) {
    .projects > .desc{font-size: 0.85rem; line-height: 150%;padding:0 25px;}
    .projects .miscWrap .tags{display: none;}
    .projects .contentWrap h3{font-size:1.25rem;margin-bottom:15px;}
    .projects .project-item:nth-child(odd), .projects .project-item:nth-child(even){margin-bottom:50px;}
    .projects .tagList{display:none;}
    .popupScreen .bgWrap .bgContent h1{font-size:1.25rem;letter-spacing:2px;}
    .popupScreen .bgWrap .bgContent p{font-size:0.75rem;letter-spacing:2px;}



}


@media screen and (max-width: 372px) {
    #botDiv .specialThanks h3{letter-spacing:5px;text-indent:9px;}
    #botDiv .specialThanks .msgBody{font-size: 0.55rem;letter-spacing:1.5px;text-indent:1.5px;}
    #botDiv .findMe .msgBody{letter-spacing:3.5px;text-indent:3.5px;font-size:0.55rem;}
    .projects .contentWrap h3{font-size:1rem;line-height: 150%;}
    .projects .miscWrap .btn{border-radius:3px;font-size:0.5rem;padding:8px 10px;letter-spacing:1px;min-width:auto;}
    .popupScreen .bgWrap .bgContent{padding: 35px 2.5%;}
    .popupScreen .bgWrap .bgContent .btn{font-size:0.5rem;padding:8px 10px;letter-spacing:1px;border-radius:3px;}
    .popupScreen .showcase{padding:2.5%;}
    .popupScreen .showcase .descLong{font-size:0.85rem;}

}





html, body { height: 100%; min-width: 320px;}
body { margin: 0px auto; -webkit-text-size-adjust: none; min-width: 320px; margin: 0 auto; font-family: "Open Sans", sans-serif; line-height: 125%; color: #252525; text-align: center;}
body * { line-height: 125%; -webkit-text-size-adjust: none; -moz-text-size-adjust: none; -ms-text-size-adjust: none; text-size-adjust: none; -webkit-text-size-adjust: auto; -moz-text-size-adjust: auto; -ms-text-size-adjust: auto; text-size-adjust: auto;-webkit-text-size-adjust: 80%;-moz-text-size-adjust: 80%;-ms-text-size-adjust: 80%;text-size-adjust: 80%;  }

body div { box-sizing: border-box; }

a { color: #ff617f; text-decoration: none;border-bottom: 1px dotted #fff; padding-bottom:2px;  transition: all 0.25s; }
a:hover { color: #fff; border-color: #ff617f; transition: all 0.25s; }
a.alt { color: #fff; border-bottom: 1px dotted #fff;  }
a.alt:hover { color: #fff; border-color: #ff617f; }

.btn { cursor: pointer; transition: background-color 0.35s 0.25s, color 0.35s 0.25s; font-size: 0.75rem; padding: 12px 15px; font-family: "Work Sans", sans-serif; display: inline-block; background-color: #ff617f; position: relative; min-width: 120px; color: #fff; text-decoration: none; font-weight: 400; text-align: center; border:1px solid #ff617f;text-transform: uppercase;text-shadow: none;border-radius: 5px; letter-spacing: 3px;}
.btn:hover { transition: background-color 0.35s 0.25s, color 0.35s 0.25s; background-color: transparent;color:#ff617f; }
.btn.alt { color: #ff617f;border:1px solid #ff617f;}
.btn.alt:hover {color: #fff; }

button { -webkit-appearance: none; -moz-appearance: none; appearance: none;border-radius:0px; }

input[type=text], input[type=email] { height:0;outline: none; border: 0px; color: #999; padding: 27px 20px; box-sizing: border-box; line-height: 100%; min-width: 325px; font-weight: 500; font-family: "Work Sans", sans-serif; font-size: 14px; }
::-webkit-input-placeholder { color: #ccc; }
::-moz-placeholder { color: #ccc; }
:-ms-input-placeholder { color: #ccc; }
:-moz-placeholder { color: #ccc; }
.form-div { font-size:0;}
.form-div > * { vertical-align:top;}

h1, h2, h3, h4, h5, h6 { font-family: "Work Sans", sans-serif; color: #252525; line-height: 125%; }

h1 { font-size: 3rem; margin: 20px 0; font-weight: 500; text-transform:uppercase; letter-spacing: 5px; }
h2 { font-size: 2rem; margin: 10px 0;text-transform:uppercase; letter-spacing: 2px;  }
h3 { font-size: 1.25rem; margin: 10px 0; text-transform:uppercase; letter-spacing: 2px;}
h4 { font-size: 16px; margin: 10px 0; }
h5 { font-size: 14px; margin: 10px 0; font-weight:900;}
h6 { font-size: 12px; margin: 10px 0; }

p { margin:20px 0; line-height: 150%;}
h1 + p { margin-top: -15px; }
p + img { margin-top:-15px;}

ul, ol { margin: 0px 0 0px 0px; padding: 0; }
ul li, ol li { margin: 0px 0 0 0px; padding: 0px 0 0 0px; line-height: 120%; }
/*.mid ul { padding: 10px 40px; list-style-type: circle; }
.mid ul li { padding: 3px 6px; }
.mid ul ul { padding: 4px 40px; }*/

hr{height:1px;background-color: #fff; opacity: 0.75;border:0px none;margin:20px 0;}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration { -webkit-appearance: none; }

.clear { clear: both; }
.rgt { float:right; }
.lft { float:left;}

.tip{font-family: "Work Sans",sans-serif;letter-spacing:3px;font-weight:100;}
.sep{ color: #ff617f;}
.no-caps{text-transform: none;}
.overlay{background: linear-gradient(180deg, rgba(25,25,25,1) 0%, rgba(0,0,0,0) 100%)  no-repeat  center top; background-size: cover; background-color: rgba(0,0,0,0.2); position:absolute; left:0;top:0;right:0;bottom:0; z-index: 2;}
.full-overlay{position:fixed;top:0%;bottom:0;left:0;right:0; height:0%;background:#aaa;z-index:1;opacity:0.75;transition: height 0.75s 0.75s;}

.popupScreen{position: absolute; top:0;bottom:0;left:0;right:0;margin:auto;z-index:4;pointer-events: none;   }
.popupScreen *{pointer-events: none;}
.popupScreen .popup{opacity: 0; z-index: 2;pointer-events:all;position:absolute;top:95px;left:0%;right:0;max-width:80%;margin:auto;transition: opacity 0.75s 0.5s;}
.popupScreen .popup .popContent{background-color:#fff;margin:0 0 50px;box-shadow: 0 0 20px 0px #000;border-radius: 5px;overflow: hidden;}
.popupScreen .popup .popContent .btn .icon{ vertical-align:baseline; margin-left:5px;display: inline-block;position: relative;width: 8px;}
.popupScreen .popup .popContent .btn .icon img{display:block;max-width: 100%;transition: 0.35s 0.25s;}
.popupScreen .popup .popContent .btn .icon img.hover{position: absolute;top:0; opacity: 0; transition: 0.35s 0.25s;}
.popupScreen .popup .popContent .btn:hover .icon img{opacity: 0; transition: opacity 0.35s 0.25s;}
.popupScreen .popup .popContent .btn:hover .icon img.hover{opacity: 1;transition: opacity 0.35s 0.25s;}
.popupScreen .misc {pointer-events: all; position: fixed; top:-100%;left:0;right:0;margin :auto;z-index: 10;background-color: #000;padding-bottom: 4px; transition: top 0.5s 0.15s;display: flex; justify-content: space-between;}
.popupScreen .misc:after {content:"";position: absolute;left:0;bottom:0;height: 0px; background-color: #ff617f; width: 100%;}
.popupScreen .misc .project{cursor: pointer; color:#fff; font-family: "Work Sans", sans-serif; text-transform: uppercase; letter-spacing: 3px;font-size: 1.5rem;display: inline-block;vertical-align: middle;padding:15px;}
.popupScreen .misc .project.next{padding-left:0px;}
.popupScreen .misc .project.prev{padding-right:0px;}
.popupScreen .misc .project img{height: 12px;}
.popupScreen .misc .closeBtn{cursor: pointer; color:#fff; font-family: "Work Sans", sans-serif; text-transform: uppercase; letter-spacing: 3px;font-size: 1.5rem;padding:15px; display: inline-block;vertical-align: middle;}
.popupScreen .bgWrap {position: relative;}
.popupScreen .bgWrap img{max-width: 100%;display: block;}
.popupScreen .bgWrap .overlay{background-image: none;background-color: #272727;opacity: 0.85;}
.popupScreen .bgWrap .bgContent{position: absolute; width: 75%;z-index: 3;top:50%;left:50%;transform:translate(-50%, -50%);text-shadow: 0 0 10px #000;}
.popupScreen .bgWrap .bgContent h1{color:#fff;font-size: 4.5rem;font-weight: 100;}
.popupScreen .bgWrap .bgContent p{color:#fff;letter-spacing: 3px;font-size: 1.75rem;}
.popupScreen .showcase{padding:25px;}
.popupScreen .showcase h2{margin:30px 0 50px;display: inline-block;}  
.popupScreen .showcase h2:after{content:"";height:1px; width:75%; margin:5px auto 0px; max-width: 250px; background-color: #ff617f;display: block;}  
.popupScreen .showcase .gallery{display: block;margin-bottom: 50px;}
.popupScreen .gallery .gallery-item{display: inline-block;width: 40%;margin:30px 2.5%;border: 1px solid #aaa;box-shadow: 0 0 10px 0 #ccc;}
.popupScreen .gallery .gallery-item img{max-width: 100%;display: block;padding: 5px; box-sizing: border-box;}
.popupScreen .showcase .descLong{margin:auto;max-width: 80%;line-height: 200%;margin:10px auto 50px; font-family: "Open Sans", sans-serif; letter-spacing: 2px; font-size: 1.25rem;}

.popupScreen.active *{pointer-events: all;}
.popupScreen.active .popup{opacity:1; transition: opacity 1s 0.85s;}
.popupScreen.active .full-overlay{height:100%;transition:height 0.75s 0.15s;}
.popupScreen.active .misc{top:0;transition:top 0.75s 1.25s;}
.popupScreen.active .misc:after{height:4px; transition:height 0.75s 2.25s;}


button.back-to-top {position:fixed;bottom:10%;z-index:3;right:5%;cursor:pointer;background-color:transparent;font-size:0;width:50px;height:50px;}
button.back-to-top .scrollTopBtn{opacity:0.5;border:1px solid #ff617f;background-color:transparent;border-radius:5px;width:50px;height:50px;position: relative;transition: 0.25s;}
button.back-to-top .scrollTopBtn:hover{opacity:1; transition: border 0.25s, opacity 0.25s;}
button.back-to-top .scrollTopBtn img{display: block; margin: auto;position: relative; top:50%;transform:translateY(-50%);width:20px;}

/*TOP*/
#topDiv { z-index: 2; width: 100%; background: #fff;top:0;position: fixed; color:#fff !important;height:500px;}
#topDiv .scrollDown{cursor: pointer; background: #ff617f no-repeat center ; background-size: auto 50%; width:50px;height:50px;display: inline-block;position: absolute; z-index: 5;bottom:-25px;border-radius: 50%;left:0;right:0;margin:auto;box-shadow: 0px 7px 15px -5px #aaa;transition:opacity 0.4s 1.5s, transform  0.4s 1.5s, background-color 0.5s;}
#topDiv .scrollDown img{opacity: 1;transition:opacity 0.5s;display:block;position: absolute;top:50%;left:50%;margin-left:-1px;transform:translate(-50%, -50%);height:25px;}
#topDiv .scrollDown img.hover{opacity: 0;transition:opacity 0.5s;}
#topDiv .scrollDown:hover{ background-color: #fff; transition: background-color 0.5s;}
#topDiv .scrollDown:hover img{opacity:0;transition:opacity 0.5s;}
#topDiv .scrollDown:hover img.hover{opacity: 1;transition:opacity 0.5s;}
#topDiv .banner{position:absolute; left:0;top:0;right:0;bottom:0; z-index: 1;}
#topDiv .banner *{position:absolute; left:0;top:0;right:0;bottom:0;}
#topDiv .banner .bg{background: #0b0c11  no-repeat  center top fixed; z-index: 1;}
#topDiv .banner .bpart1{background:no-repeat center top fixed; background-size: 1200px auto; z-index: 2; }
#topDiv .banner .bpart2{background: no-repeat center top fixed; background-size: 1200px auto; z-index: 3; }
#topDiv .banner .bpart3{background: no-repeat center top fixed; background-size: 1200px auto; z-index: 4; }
#topDiv .banner .bpart4{background:no-repeat center top -150px fixed; background-size:1200px auto; z-index: 5; }
#topDiv .banner .bpart5{background:no-repeat center top -150px fixed; background-size:1200px auto; z-index: 6; }
#topDiv .banner .bpart6{background:no-repeat  left top 0px; background-size: 1200px auto; z-index: 7; }
#topDiv .profile{position:absolute;z-index:3;transform:translate(-50%, -50%);top:50%;margin:auto;left:50%;width:auto !important;}
#topDiv .profile h1{display:inline-block; margin:0 0 0px;text-transform:uppercase;font-size:3rem;letter-spacing:4.5px;color:#fff;text-shadow: 5px 5px 5px #000;}
#topDiv .profile .bio span{ display: inline-block; }
#topDiv .profile.parallaxInit span{transition:none;}
#topDiv .profile .role{ text-transform:uppercase; color:#fff; letter-spacing: 5px; white-space: nowrap;}
#topDiv .profile .desc{ margin-top:5px;letter-spacing: 3px;white-space: nowrap;}
#topDiv .logo{ position: fixed;  z-index: 4; left:50px;top:30px;}
#topDiv .socialWrap{position: relative;display: inline-block;}
#topDiv .socials ul{list-style: none;margin:0;padding:0;}
#topDiv .socials ul li{display: inline-block;margin:5px 5px 0;}
#topDiv .socials ul li a{display: block; text-decoration:none; border:0px none;border-radius: 50%; }
#topDiv .socials a img{height:25px; -webkit-filter:invert(1); filter:invert(1); transition:-webkit-filter 0.25s 0.25s; transition:filter 0.25s 0.25s; transition:filter 0.25s 0.25s, -webkit-filter 0.25s 0.25s;display: block;} 
#topDiv .socials a:hover img{  -webkit-filter:invert(50%) sepia(10%) saturate(50) hue-rotate(-65deg) brightness(90%);  filter:invert(50%) sepia(10%) saturate(50) hue-rotate(-65deg) brightness(90%); transition:-webkit-filter 0.25s 0.25s; transition:filter 0.25s 0.25s; transition:filter 0.25s 0.25s, -webkit-filter 0.25s 0.25s;} 

/*END TOP*/


/*BOT*/

#botDiv { border-top:5px #ebd7db solid;background-color:#242424;background-size:cover;background-repeat:no-repeat;background-position:bottom right;background-attachment:fixed;position:relative;}
#botDiv .overlay{bottom:65px;}
#botDiv .footerDiv{background:#202020;border-top:4px #ff617f solid;color:#fff;font-size:0.75rem; padding:15px 50px;display:flex;align-content:space-between;align-items:center;justify-content:center;}
#botDiv .footerDiv .foot-item{vertical-align:middle;flex:1 1;}
#botDiv .footerDiv .foot-item.designBy{text-align: right;flex:2 1;}
#botDiv .footerDiv .foot-item.reactSol{text-align: left;}
#botDiv .footerDiv .foot-item > *{vertical-align:middle;}
#botDiv .footerDiv .foot-item span.sep{vertical-align:baseline;}
#botDiv .findMe{color:#fff;font-size:0.75rem;padding:150px 15px 10px;position:relative;z-index:3; vertical-align:middle; }
#botDiv .findMe h2{color:#fff; letter-spacing: 4px;margin-bottom:5px;text-indent: 4px;}
#botDiv .specialThanks{color:#fff; font-size:0.75rem;padding:10px 15px 150px;position:relative;z-index:3; vertical-align:middle; }
#botDiv .specialThanks h3{color:#fff; letter-spacing: 9px;margin-bottom:5px; text-indent: 9px;}
#botDiv .findMe .msgBody{letter-spacing: 4px; text-indent: 4px;display: block;}
#botDiv .specialThanks .msgBody{letter-spacing: 1.5px;text-indent: 1.5px; display: block;}
/*END BOT*/

/*IOS*/
@supports (-webkit-touch-callout: none) {
    
    #topDiv .banner .bpart1, #topDiv .banner .bpart2,
    #topDiv .banner .bpart3, #topDiv .banner .bpart4,
    #topDiv .banner .bpart5{background-attachment:scroll; }
    #botDiv { background-attachment:scroll;}
}

@media screen and (max-width: 1800px) {
    body{font-size: 15px;}
    .popupScreen .bgWrap .bgContent h1{font-size:3rem}
    .popupScreen .bgWrap .bgContent p{font-size:1.25rem}

}

@media screen and (max-width: 1700px) {
    .popupScreen .misc .closeBtn, .popupScreen .misc .project{font-size: 1.25rem;}
}

@media screen and (max-width: 1600px) {
}


@media screen and (max-width: 1440px) {

    body{font-size: 14px;}
    button.back-to-top{bottom:5%;right:2.5%;}
